
















 import util from '../../common/util'
import navigate from '../../components/navigator'
export default {
    data() {
        return {
           titleText:'',
           showStar:true,
           showShare:true,
           imgList:[]
        }
    },
    onLoad(option) {
        let self = this;
        self.titleText = option.movieName;
        self.movieId = option.movieId;
    },
    onReady(){
        let self = this;
        util.request({                
            url:'https://api-m.mtime.cn/Movie/ImageAll.api?movieId='+self.movieId
        }).then(res=>{
            self.imgList = res.images;
        })
    },
    components:{
        navigate
    },
    methods:{
    }
}
